import { useEffect, useState } from 'react';
import { useRouter } from 'next/router';
import { useToast } from '../../../components/ui/ToastProvider';
import styles from './[orderId].module.css';

interface OrderItem {
  productId?: { name: string };
  name: string;
  quantity: number;
  price: number;
  total: number;
}

interface Order {
  _id: string;
  orderNumber: string;
  customerId: {
    name: string;
    email: string;
    phone: string;
  } | null;
  vendorId: {
    _id: string;
    storeName: string;
  } | null;
  items: OrderItem[];
  pricing: {
    subtotal: number;
    deliveryFee?: number;
    tax?: number;
    totalAmount: number;
  };
  delivery?: {
    address?: string;
    instructions?: string;
  };
  status: 'pending' | 'processing' | 'completed' | 'cancelled';
  notes?: string;
  createdAt: string;
  updatedAt: string;
}

export default function VendorOrderDetails() {
  const router = useRouter();
  const { orderId } = router.query;
  const [order, setOrder] = useState<Order | null>(null);
  const [loading, setLoading] = useState(true);
  const { pushToast } = useToast();

  const vendorId = typeof window !== 'undefined'
    ? (localStorage.getItem('cc_vendorId') || localStorage.getItem('vendorId'))
    : null;

  useEffect(() => {
    if (orderId && vendorId) {
      fetchOrderDetails();
    }
  }, [orderId, vendorId]);

  const fetchOrderDetails = async () => {
    try {
      setLoading(true);
      const res = await fetch(`http://localhost:5000/api/orders/${orderId}`);
      
      if (!res.ok) {
        throw new Error('Failed to fetch order details');
      }
      
      const data = await res.json();
      
      // Verify order belongs to this vendor
      if (data.vendorId?._id !== vendorId) {
        pushToast({ 
          type: 'error', 
          title: 'Access Denied', 
          message: 'This order does not belong to your store' 
        });
        router.push('/vendor/ordershistory');
        return;
      }
      
      setOrder(data);
    } catch (error: any) {
      pushToast({ 
        type: 'error', 
        title: 'Error', 
        message: error.message || 'Failed to load order details' 
      });
      router.push('/vendor/ordershistory');
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    return date.toLocaleDateString('en-IN', {
      day: '2-digit',
      month: 'short',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const formatShortOrderId = (orderNumber: string) => {
    // Extract last 6 chars for display
    return orderNumber.slice(-6).toUpperCase();
  };

  const getStatusLabel = (status: string) => {
    const labels: Record<string, string> = {
      pending: 'Pending',
      processing: 'Processing',
      completed: 'Completed',
      cancelled: 'Cancelled'
    };
    return labels[status] || status;
  };

  const handleBack = () => {
    router.push('/vendor/ordershistory');
  };

  if (loading) {
    return (
      <div className={styles.orderDetailsContainer}>
        <div className={styles.loadingState}>Loading order details...</div>
      </div>
    );
  }

  if (!order) {
    return (
      <div className={styles.orderDetailsContainer}>
        <div className={styles.errorState}>Order not found</div>
      </div>
    );
  }

  return (
    <div className={styles.orderDetailsContainer}>
      <div className={styles.orderDetailsHeader}>
        <button onClick={handleBack} className={styles.backButton}>
          ← Back to Order History
        </button>
      </div>

      <div className={styles.orderBillCard}>
        {/* ORDER HEADER */}
        <div className={styles.billHeader}>
          <div className={styles.billTitleSection}>
            <h1 className={styles.billTitle}>Order Details</h1>
            <div className={styles.orderIdDisplay}>
              Order #{formatShortOrderId(order.orderNumber)}
            </div>
          </div>
          <div className={`${styles.orderStatusBadge} ${styles[order.status]}`}>
            {getStatusLabel(order.status)}
          </div>
        </div>

        {/* ORDER TIMESTAMPS */}
        <div className={styles.billSection}>
          <h2 className={styles.billSectionTitle}>Order Information</h2>
          <div className={styles.billInfoGrid}>
            <div className={styles.billInfoItem}>
              <span className={styles.billInfoLabel}>Ordered:</span>
              <span className={styles.billInfoValue}>{formatDate(order.createdAt)}</span>
            </div>
            {order.status === 'completed' && (
              <div className={styles.billInfoItem}>
                <span className={styles.billInfoLabel}>Completed:</span>
                <span className={styles.billInfoValue}>{formatDate(order.updatedAt)}</span>
              </div>
            )}
            {order.status === 'cancelled' && (
              <div className={styles.billInfoItem}>
                <span className={styles.billInfoLabel}>Cancelled:</span>
                <span className={styles.billInfoValue}>{formatDate(order.updatedAt)}</span>
              </div>
            )}
          </div>
        </div>

        {/* CUSTOMER DETAILS */}
        <div className={styles.billSection}>
          <h2 className={styles.billSectionTitle}>Customer Details</h2>
          <div className={styles.billInfoGrid}>
            <div className={styles.billInfoItem}>
              <span className={styles.billInfoLabel}>Name:</span>
              <span className={styles.billInfoValue}>{order.customerId?.name || '—'}</span>
            </div>
            {order.customerId?.phone && (
              <div className={styles.billInfoItem}>
                <span className={styles.billInfoLabel}>Phone:</span>
                <span className={styles.billInfoValue}>{order.customerId.phone}</span>
              </div>
            )}
            {order.delivery?.address && (
              <div className={`${styles.billInfoItem} ${styles.fullWidth}`}>
                <span className={styles.billInfoLabel}>Delivery Address:</span>
                <span className={styles.billInfoValue}>{order.delivery.address}</span>
              </div>
            )}
          </div>
        </div>

        {/* ITEMIZED TABLE */}
        <div className={styles.billSection}>
          <h2 className={styles.billSectionTitle}>Order Items</h2>
          <div className={styles.billTableWrapper}>
            <table className={styles.billTable}>
              <thead>
                <tr>
                  <th className={styles.itemNameCol}>Item Name</th>
                  <th className={styles.itemPriceCol}>Price</th>
                  <th className={styles.itemQtyCol}>Qty</th>
                  <th className={styles.itemTotalCol}>Total</th>
                </tr>
              </thead>
              <tbody>
                {order.items.map((item, index) => {
                  const itemName = item.productId?.name || item.name || 'Unknown Item';
                  const itemPrice = item.price || 0;
                  const itemQty = item.quantity || 0;
                  const itemTotal = item.total || (itemPrice * itemQty);

                  return (
                    <tr key={index}>
                      <td className={styles.itemNameCell}>{itemName}</td>
                      <td className={styles.itemPriceCell}>₹{itemPrice.toFixed(2)}</td>
                      <td className={styles.itemQtyCell}>{itemQty}</td>
                      <td className={styles.itemTotalCell}>₹{itemTotal.toFixed(2)}</td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>

        {/* ORDER SUMMARY */}
        <div className={styles.billSection}>
          <div className={styles.billSummary}>
            <div className={styles.billSummaryRow}>
              <span className={styles.billSummaryLabel}>Subtotal:</span>
              <span className={styles.billSummaryValue}>₹{order.pricing.subtotal.toFixed(2)}</span>
            </div>
            {order.pricing.deliveryFee && order.pricing.deliveryFee > 0 && (
              <div className={styles.billSummaryRow}>
                <span className={styles.billSummaryLabel}>Delivery Fee:</span>
                <span className={styles.billSummaryValue}>₹{order.pricing.deliveryFee.toFixed(2)}</span>
              </div>
            )}
            {order.pricing.tax && order.pricing.tax > 0 && (
              <div className={styles.billSummaryRow}>
                <span className={styles.billSummaryLabel}>Tax:</span>
                <span className={styles.billSummaryValue}>₹{order.pricing.tax.toFixed(2)}</span>
              </div>
            )}
            <div className={`${styles.billSummaryRow} ${styles.billTotal}`}>
              <span className={styles.billSummaryLabel}>Total:</span>
              <span className={styles.billSummaryValue}>₹{order.pricing.totalAmount.toFixed(2)}</span>
            </div>
          </div>
        </div>

        {/* NOTES */}
        {(order.delivery?.instructions || order.notes) && (
          <div className={styles.billSection}>
            <h2 className={styles.billSectionTitle}>Notes</h2>
            <div className={styles.billNotes}>
              {order.delivery?.instructions && (
                <div className={styles.billNoteItem}>
                  <strong>Delivery Instructions:</strong> {order.delivery.instructions}
                </div>
              )}
              {order.notes && (
                <div className={styles.billNoteItem}>
                  <strong>Order Notes:</strong> {order.notes}
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
